<template>
  <div class="patrolForm">
    <h3>Rapport d'activité</h3>
    <form>
      <vs-input disabled label="Votre pseudo sur Habbo" v-model="this.$parent.accountData.user.name"/>
      <vs-input-number label="Numéro du rapport" type="number" v-model="form.id" required/>
      <vs-input label="Coéquipier(s)/Coéquipière(s)" placeholder="Ex: Guide002" v-model="form.assistant"/>

      <vs-divider>Appart n°1</vs-divider>
      <vs-input label="Propriétaire" placeholder="Ex: Guide001" v-model="form.first.owner"/>
      <vs-input label="Commentaires" placeholder="Ex: R.A.S" v-model="form.first.comment"/>
      <vs-input :danger="invalidScreen1" danger-text="Veuillez entrer un lien valide" label="Lien vers l'image de votre screen" placeholder="Ex: http://abc.xyz/image.png" v-model="form.first.screen" required/><br/>

      <vs-divider>Appart n°2</vs-divider>
      <vs-input label="Propriétaire" placeholder="Ex: Guide001" v-model="form.second.owner"/>
      <vs-input label="Commentaires" placeholder="Ex: R.A.S" v-model="form.second.comment"/>
      <vs-input :danger="invalidScreen2" danger-text="Veuillez entrer un lien valide" label="Lien vers l'image de votre screen" placeholder="Ex: http://abc.xyz/image.png" v-model="form.second.screen" required/><br/>

      <vs-divider>Appart n°3</vs-divider>
      <vs-input label="Propriétaire" placeholder="Ex: Guide001" v-model="form.third.owner"/>
      <vs-input label="Commentaires" placeholder="Ex: R.A.S" v-model="form.third.comment"/>
      <vs-input :danger="invalidScreen3" danger-text="Veuillez entrer un lien valide" label="Lien vers l'image de votre screen" placeholder="Ex: http://abc.xyz/image.png" v-model="form.third.screen" required/><br/>
      <vs-button color="success" type="relief" @click="sendMsg()">Envoyer</vs-button>
    </form>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'PatrolForm',
  components: {
  },
  data: function() {
    return {
      form: {
        message:"",
        date:"",
        time:"",
        id:"0",
        assistant:"",
        first: {
          owner:"",
          comment:"",
          screen:""
        },
        second: {
          owner:"",
          comment:"",
          screen:""
        },
        third: {
          owner:"",
          comment:"",
          screen:""
        }
      },
      invalidScreen1: false,
      invalidScreen2: false,
      invalidScreen3: false,
      stopState: false,
    }
  },
  methods: {
    sendMsg: function(){

      if(!this.validURL(this.form.first.screen)) { //Checking if it is a valid URL for each screen
        this.invalidScreen1 = true
        return
      }else if(!this.validURL(this.form.second.screen)) {
        this.invalidScreen2 = true
        return
      }else if(!this.validURL(this.form.third.screen)) {
        this.invalidScreen3 = true
        return
      }else{
        this.invalidScreen1 = false
        this.invalidScreen2 = false
        this.invalidScreen3 = false
      }
      // Calculating date and time
      this.form.time = this.getTime
      this.form.date = this.getDate
      // Setting the message up
      this.form.message = "[center]:moyen: [/center]" + '\n'
      + '\n'
      + "[center][color=#BF2E32][u][b][size=16]Patrouille n°" + this.form.id + " de " + this.$parent.accountData.user.name + "[/size][/b][/u][/color][/center]" + '\n'
      + '\n'
      + "[left]:pass-bleu::pass-blanc::pass-rouge: Coéquipier/Coéquipière : [b]" + this.form.assistant + "[/b][/left]" + '\n'
      + '\n'
      + "[left][u][b]Appart n°1[/b][/u][/left]" + '\n'
      + "[left]:Vers la droite Propriétaire : [b]" + this.form.first.owner + "[/b][/left]" + '\n'
      + "[left]:Vers la droite Commentaire : [b]" + this.form.first.comment + "[/b][/left]" + '\n'
      + "[left]:idée: Screen :[/left]" + '\n'
      + "[left][spoiler=\"Screen\"][img]" + this.form.first.screen + "[/img][/spoiler][/left]" + '\n'
      + '\n'
      + "[left][u][b]Appart n°2[/b][/u][/left]" + '\n'
      + "[left]:Vers la droite Propriétaire : [b]" + this.form.second.owner + "[/b][/left]" + '\n'
      + "[left]:Vers la droite Commentaire : [b]" + this.form.second.comment + "[/b][/left]" + '\n'
      + "[left]:idée: Screen :[/left]" + '\n'
      + "[left][spoiler=\"Screen\"][img]" + this.form.second.screen + "[/img][/spoiler][/left]" + '\n'
      + '\n'
      + "[left][u][b]Appart n°3[/b][/u][/left]" + '\n'
      + "[left]:Vers la droite Propriétaire : [b]" + this.form.third.owner + "[/b][/left]" + '\n'
      + "[left]:Vers la droite Commentaire : [b]" + this.form.third.comment + "[/b][/left]" + '\n'
      + "[left]:idée: Screen :[/left]" + '\n'
      + "[left][spoiler=\"Screen\"][img]" + this.form.third.screen + "[/img][/spoiler][/left]" + '\n'
      + "[left]" + '\n'
      + "[center]:moyen: [/center]" + '\n'
      + "[/left]" + '\n'

      // Copying the BBCode
      this.$copyText(this.form.message)
      this.$vs.notify({title:'Succès !',text:'Message copié !',color:'success',position:'top-center'})
    },
    // Number beautifier
    addZero: function(i) {
      if (i < 10) {
        i = "0" + i;
      }
      return i
    },
    //URL check
    validURL: function(str) {
      var pattern = new RegExp('^(https?:\\/\\/)?'+ // protocol
        '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|'+ // domain name
        '((\\d{1,3}\\.){3}\\d{1,3}))'+ // OR ip (v4) address
        '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*'+ // port and path
        '(\\?[;&a-z\\d%_.~+=-]*)?'+ // query string
        '(\\#[-a-z\\d_]*)?$','i'); // fragment locator
      return !!pattern.test(str);
    },
  },
  computed: {
    // Get the time like 12:00
    getTime: function() {
      var d = new Date();
      var h = this.addZero(d.getHours());
      var m = this.addZero(d.getMinutes());
      d = h + ":" + m
      return d
    },
    getDate: function() {
      var today = new Date();
      var dd = String(today.getDate()).padStart(2, '0');
      var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
      var yyyy = today.getFullYear();
      today = dd + '/' + mm + '/' + yyyy
      return today
    },
  },
  beforeCreate: function() {
    this.$parent.checkConnected()
  },
}
</script>
<style lang="stylus">
form
  margin-right auto
  margin-left auto
  display inline-block
.patrolForm .vs-input, .vs-input-number
  margin-top 10px
  width 500px
  text-align left
</style>
